import { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import "./india.css";
const India = () => {
  const [data, setData] = useState([]);

  const getCovidData = async () => {
    try {
      const res = await fetch("https://data.covid19india.org/data.json");
      const actualData = await res.json();
      setData(actualData.statewise[0]);
    } catch (error) {
      console.log("err");
    }
  };

  useEffect(() => {
    getCovidData();
  }, []);
  return (
    <div className="maindiv">
      <h1>India-Total</h1>
      <div className="row">
        <div className="col-md-3">
          <div className="card text-center" style={{ background:'blue', color:'white'}}>
            <h4>TOTAL CONFIRMED</h4>
            <h2>{data.confirmed}</h2>
          </div>
        </div>

        <div className="col-md-3">
          <div className="card text-center" style={{ background:'orange', color:'white'}}>
            <h4>ACTIVE CASES</h4>
            <h2>{data.active}</h2>
          </div>
        </div>

        <div className="col-md-3">
          <div className="card text-center" style={{ background:'green', color:'white'}}>
            <h4>TOTAL RECOVERED</h4>
            <h2>{data.recovered}</h2>
          </div>
        </div>

        <div className="col-md-3">
          <div className="card text-center" style={{ background:'red', color:'white'}}>
            <h4>TOTAL DEATHS</h4>
            <h2>{data.deaths}</h2>
          </div>
        </div>
      </div>
    </div>
  );
};

export default India;
